/*--------------------------------

    Layout

*/
/*

use:

    get the value of a particular breakpoint

example:

    .box {
        width: breakpoint("phone-wide");
    }

    would generate:

    .box {
        width: 480px;
    }

*/
/*--------------------------------

    Colours

*/
/*--------------------------------

    Typography

*/
/*
ordered from high to low
suggested naming convention would be the class/ID the z-index is going on
*/
/*

    Reset

*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*--------------------------------

    Media Queries

    used for outputting content either between media query tags, or without (for fixed-width.scss)

    example: basic usage

    .element {
        width: 50%;

        @include mq("tablet-small") {
            width: 20%;
        }
    }

    example: using max-width

    .element {
        width: 50%;

        @include mq("tablet-small", "max") {
            width: 20%;
        }
    }

*/
/*--------------------------------

    Responsive ratio

    Used for creating scalable elements that maintain the same ratio

    example:
    .element {
        @include responsive-ratio(400, 300);
    }

*/
/*--------------------------------

    Typography

    Text image replacement, with responsive ratio

    HTML:

    <h1 class="element">
        <span>Text to replace</span>
    </h1>

    example:
    .element {
        @include typography(200, 50, 'hello-world');
    }


*/
/*--------------------------------

    CSS Triangle

    used for creating CSS only triangles

    example:
    .element {

        &:before {
            @include css-triangle(blue, down);
        }
    }

*/
/*--------------------------------

    Helper mixins

*/
/*--------------------------------

    Misc

*/
/*--------------------------------

    Box sizing

*/
*, *:before, *:after {
  box-sizing: border-box;
}

/*--------------------------------

    Basic document styling

*/
html {
  font-size: 10px;
  min-height: 100%;
  overflow-y: scroll;
  -webkit-text-size-adjust: none;
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media only screen and (max-width: phone-wide) {
  html {
    font-size: 9px;
  }
}

body {
  background-color: #fff;
  color: #1a1a1a;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
}
body:after {
  content: " ";
  display: block;
  clear: both;
}

/*--------------------------------

    Text selection/highlighting

*/
::-moz-selection {
  background: #333;
  color: #fff;
  text-shadow: none;
}

::selection {
  background: #333;
  color: #fff;
  text-shadow: none;
}

.wrap {
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}
.wrap:after {
  content: " ";
  display: block;
  clear: both;
}

.blog-list > div {
  padding: 2rem;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .blog-list > div {
    width: 48.33333%;
    float: left;
    margin-left: 0.83333%;
    margin-right: 0.83333%;
    margin-top: 0.83333%;
    margin-bottom: 0.83333%;
  }
  .blog-list > div:nth-child(5n+1) {
    width: 98.33333%;
    float: left;
    margin-left: 0.83333%;
    margin-right: 0.83333%;
  }
}
@media (min-width: 1025px) {
  .blog-list > div {
    width: 31.66667%;
    float: left;
    margin-left: 0.83333%;
    margin-right: 0.83333%;
    margin-top: 0.83333%;
    margin-bottom: 0.83333%;
  }
  .blog-list > div:nth-child(10n+1), .blog-list > div:nth-child(10n+7) {
    width: 65%;
    float: left;
    margin-left: 0.83333%;
    margin-right: 0.83333%;
  }
}

.blog-list > div {
  background: grey;
  height: 200px;
}

.image-list > div {
  padding: 2rem;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .image-list > div {
    width: 50%;
    float: left;
  }
  .image-list > div:nth-child(5n+1) {
    width: 100%;
    float: left;
  }
}
@media (min-width: 1025px) {
  .image-list > div {
    width: 33.33333%;
    float: left;
  }
  .image-list > div:nth-child(10n+1), .image-list > div:nth-child(10n+7) {
    width: 66.66667%;
    float: left;
  }
}

.image-list > div {
  background: grey;
  border: 1px solid #333;
  height: 200px;
}
